import { Link } from 'react-router-dom';

import Grid from './Grid';

const footerAboutLinks = [
  {
    display: 'Gii thiu',
    path: '/about',
  },
  {
    display: 'Lin h',
    path: '/contact',
  },
  {
    display: 'Tuyn dng',
    path: '/about',
  },
  {
    display: 'Tin tc',
    path: '/about',
  },
  {
    display: 'H thng ca hng',
    path: '/about',
  },
];

const footerCustomerLinks = [
  {
    display: 'Chnh sch i tr',
    path: '/about',
  },
  {
    display: 'Chnh sch bo hnh',
    path: '/about',
  },
  {
    display: 'Chnh sch hon tin',
    path: '/about',
  },
];

const Footer = () => {
  return (
    <footer>
      <div className="container">
        <Grid col={4} mdCol={2} smCol={1} gap={30}>
          <div className="footer__about">
            <div className="footer__logo">
              <p>
                <Link to="/">
                  <span>Shop Cy Cnh</span>
                </Link>
              </p>
            </div>
            <p>
              orem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut
              enim ad minim veniam, quis nostrud exercitation ullamco laboris
              nisi ut aliquip ex ea commodo consequat.
            </p>
          </div>
          <div>
            <div className="footer__title">V cng ty ca chng ti</div>
            <div className="footer__content">
              {footerAboutLinks.map((item, index) => (
                <p key={index}>
                  <Link to={item.path}>{item.display}</Link>
                </p>
              ))}
            </div>
          </div>
          <div>
            <div className="footer__title">Chm sc khch hng</div>
            <div className="footer__content">
              {footerCustomerLinks.map((item, index) => (
                <p key={index}>
                  <Link to={item.path}>{item.display}</Link>
                </p>
              ))}
            </div>
          </div>
          <div>
            <div className="footer__title">Tng i h tr</div>
            <div className="footer__content">
              <p>
                Lin h t hng <strong>0987654321</strong>
              </p>
              <p>
                Thc mc t hng <strong>0987654321</strong>
              </p>
              <p>
                Gp , khiu ni <strong>0987654321</strong>
              </p>
            </div>
          </div>
        </Grid>
      </div>
    </footer>
  );
};

export default Footer;
